</template>

<script>
import { defineComponent, reactive, onBeforeMount, onMounted } from 'vue'
import { L2Dwidget } from '../../static/live2d/L2Dwidget.min.js'
import { webpackJsonpL2Dwidget } from '../../static/live2d/L2Dwidget.0.min.js'

export default defineComponent({
  name: 'Live2d',
  setup () {
    const state = reactive({
      user: '',
      password: '',
      form: {
          account: '',
          password: ''
      }
    })
    onBeforeMount(() => {
        console.log(L2Dwidget)
    })
    onMounted(() => {
        init()
    })
    function init () {
        L2Dwidget.init({
            "model": {
                jsonPath: "https://unpkg.com/live2d-widget-model-wanko/assets/wanko.model.json",//
                "scale": 1
            },
            "display": {
                "position": "left",//
                // "width": 200,
                // "height": 500,
                // "hOffset": 70,
                // "vOffset": 70
            },
            "mobile": {
                "show": true,
                "scale": 0.5
            },
            "react": {
                "opacityDefault": 0.7,//
                "opacityOnHover": 0.2
            }
        });
    }
    return { state }
  }
})
</script>

